/* eslint-disable jsx-a11y/label-has-associated-control */
import React, { FC, useEffect, useState } from 'react'
import {
  ref,
  uploadBytes,
  getDownloadURL,
  deleteObject,
} from 'firebase/storage'
import { v4 } from 'uuid'

import { isTextAreaElement } from '../../types'
import { storage } from '../../firebase'
import cnTodoForm from './TodoForm.classname'

import './TodoForm.css'

type TodoInputProps = {
  onCreate: (
    header: string,
    description: string,
    file: string,
    deadline: string,
    completed: boolean
  ) => void
}

const TodoForm: FC<TodoInputProps> = ({ onCreate }) => {
  const [form, setForm] = useState({
    header: '',
    description: '',
    file: '',
    deadline: '2022-12-31',
  })
  const [isLoad, setIsLoad] = useState(false)
  const [isValid, setIsValid] = useState(false)
  const [url, setUrl] = useState('')

  useEffect(() => {
    //  ,       
    if (form.header && form.description) {
      setIsValid(true)
    } else {
      setIsValid(false)
    }
  }, [form])

  const handleInputChange = (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { target } = event
    if (isTextAreaElement(target)) {
      setForm((prev) => ({ ...prev, [target.name]: target.value }))

      return
    }
    setForm((prev) => ({
      ...prev,
      [target.name]: target.value,
    }))
  }

  const handleFileDelete = () => {
    //     storage -->  
    const desertRef = ref(storage, url)

    deleteObject(desertRef)

    setForm((prev) => ({
      ...prev,
      file: '',
    }))

    setUrl('')
  }

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { target } = event

    //   
    setForm((prev) => ({
      ...prev,
      [target.name]: target.value,
    }))

    //      storage
    const file = target.files

    //     
    if (file) {
      setIsLoad(true)

      const currentFile = file[0]
      const imageRef = ref(storage, `todos/${currentFile.name + v4()}`)

      uploadBytes(imageRef, currentFile).then((snapshot) => {
        getDownloadURL(snapshot.ref).then((urlFile) => {
          setIsLoad(false)

          setUrl(urlFile)
        })
      })
    }
  }

  const handleSubmitTask = (event: React.FormEvent) => {
    event.preventDefault()

    const { header, description, deadline } = form

    onCreate(header, description, url, deadline, false)

    setUrl('')
    setForm((prev) => ({
      ...prev,
      header: '',
      description: '',
      file: '',
    }))
  }

  return (
    <form className={cnTodoForm()} onSubmit={handleSubmitTask}>
      <input
        className={cnTodoForm('Input')}
        type="text"
        name="header"
        placeholder=" "
        value={form.header}
        onChange={handleInputChange}
      />
      <textarea
        name="description"
        value={form.description}
        onChange={handleInputChange}
        placeholder="  "
        className={cnTodoForm('Textarea')}
      />
      <div className={cnTodoForm('File')}>
        <p className={cnTodoForm('FileTitle')}>
          {form.file || ' '}
        </p>
        {url ? (
          <button
            type="button"
            className={cnTodoForm('FileButton')}
            onClick={handleFileDelete}
          >
             
          </button>
        ) : (
          <div className={cnTodoForm('FileWrapper')}>
            <input
              id="file"
              disabled={isLoad}
              value={form.file}
              className={cnTodoForm('FileInput')}
              type="file"
              accept=".png, .jpg, .jpeg"
              onChange={handleFileChange}
              name="file"
            />
            <label htmlFor="file" className={cnTodoForm('FileLabel')}>
               
            </label>
          </div>
        )}
      </div>
      <div className={cnTodoForm('Date')}>
        <p className={cnTodoForm('DateTitle')}> </p>
        <input
          className={cnTodoForm('DateInput')}
          id="date"
          type="date"
          name="deadline"
          value={form.deadline}
          onChange={handleInputChange}
          min="2022-01-01"
          max="2040-12-31"
        />
      </div>
      <button
        className={cnTodoForm('Button')}
        disabled={isLoad || !isValid}
        type="submit"
      >
         
      </button>
    </form>
  )
}

export default TodoForm
